/* Base Accordion Styles */
.jf-accordion {
  border-bottom: 1px solid var(--jf-naturalLight-300);
  transition: all 0.3s ease;
  position: relative;
}

/* Variants */
.jf-accordion--bordered {
  border: 1px solid var(--jf-naturalLight-300);
  border-radius: 8px;
  margin-bottom: 8px;
  overflow: hidden;
}

.jf-accordion--bordered:not(:last-child) {
  margin-bottom: 12px;
}

.jf-accordion--elevated {
  border: none;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  margin-bottom: 16px;
  overflow: hidden;
  background: var(--jf-naturalLight-000);
}

.jf-accordion--elevated:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}




/* Disabled State */
.jf-accordion--disabled {
  opacity: 0.6;
  pointer-events: none;
}

.jf-accordion--disabled .jf-accordion-header {
  cursor: not-allowed;
}

/* Header Styles */
.jf-accordion-header {
  padding: 16px;
  cursor: pointer;
  font-size: 1.4rem;
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: all 0.3s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  outline: none;
}

.jf-accordion-header:hover {
  background-color: var(--jf-naturalLight-050);
}

.jf-accordion-header:focus-visible {
  outline: 2px solid var(--jf-Cerulean-500);
  outline-offset: 2px;
}

.jf-accordion-header.active {
  background-color: var(--jf-naturalLight-050);
}

/* Icon Styles */
.jf-accordion-header__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.jf-accordion-header__icon svg {
  display: block;
  color: var(--jf-naturalDark-1300);
  transition: color 0.3s ease;
}

.jf-accordion-header:hover .jf-accordion-header__icon svg {
  color: var(--jf-Cerulean-500);
}

/* Number Styles */
.jf-accordion-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background-color: var(--jf-Cerulean-100);
  color: var(--jf-Cerulean-700);
  border-radius: 50%;
  font-size: 1.2rem;
  font-weight: 600;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.jf-accordion-header:hover .jf-accordion-number {
  background-color: var(--jf-Cerulean-200);
  color: var(--jf-Cerulean-800);
}

/* Chevron Styles */
.jf-accordion-chevron {
  color: var(--jf-naturalDark-1300);
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.jf-accordion-chevron--left {
  margin-right: auto;
}

.jf-accordion-chevron--right {
  margin-left: auto;
}

.jf-accordion-header:hover .jf-accordion-chevron {
  color: var(--jf-Cerulean-500);
}

.jf-accordion-header.active .jf-accordion-chevron {
  transform: rotate(180deg);
  color: var(--jf-Cerulean-500);
}

/* Title Styles */
.jf-accordion-header__title {
  line-height: 1.5;
  font-size: 1.4rem;
  font-family: var(--jf-medium-font);
  color: var(--jf-naturalDark-1300);
  margin: 0;
  flex: 1;
  transition: color 0.3s ease;
}

.jf-accordion-header:hover .jf-accordion-header__title {
  color: var(--jf-Cerulean-600);
}

.jf-accordion-header.active .jf-accordion-header__title {
  color: var(--jf-Cerulean-600);
}

/* Body Styles */
.jf-accordion-body {
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  height: auto;
  max-height: 1000px;
  opacity: 1;
}

.jf-accordion-body.collapsed {
  max-height: 0;
  opacity: 0;
  padding: 0;
}

.jf-accordion-body_content {
  padding: 16px;
  font-size: 1.3rem;
  line-height: 1.6;
  color: var(--jf-naturalDark-900);
  font-family: var(--jf-regular-font);
}

/* Legacy Support */
.jf-accordion-left--icon {
  display: flex;
  gap: 12px;
  align-items: center;
}

.jf-accordion-headaer__icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.jf-accordion-headaer__icon svg {
  display: block;
  color: var(--jf-naturalDark-1300);
}

/* Responsive Design */
@media (max-width: 768px) {
  .jf-accordion-header {
    padding: 12px 16px;
    font-size: 1.3rem;
  }
  
  .jf-accordion-header__title {
    font-size: 1.3rem;
  }
  
  .jf-accordion-body_content {
    padding: 12px 16px;
    font-size: 1.3rem;
  }
  
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
  .jf-accordion {
    border-bottom: 2px solid var(--jf-naturalDark-1300);
  }
  
  .jf-accordion--bordered {
    border: 2px solid var(--jf-naturalDark-1300);
  }
  
  .jf-accordion-header:focus-visible {
    outline: 3px solid var(--jf-naturalDark-1300);
  }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  .jf-accordion,
  .jf-accordion-header,
  .jf-accordion-body,
  .jf-accordion-chevron,
  .accordion-header__title,
  .jf-accordion-header__icon svg,
  .jf-accordion-number {
    transition: none;
  }
}
/* Popover Base Styles */
.jf-popover {
  position: relative;
  display: inline-block;
}

.jf-popover-trigger {
  cursor: pointer;
}

.jf-popover--disabled .jf-popover-trigger {
  cursor: not-allowed;
  opacity: 0.6;
}

.jf-popover-portal {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 1000;
}

.jf-popover-content {
  pointer-events: auto;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  animation: popover-enter 0.15s ease-out;
}

@keyframes popover-enter {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-8px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* Dropdown Container */
.jf-dropdown-container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.jf-dropdown-label {
  margin-bottom: 0.4rem;
  font-size: 1.4rem;
  font-weight: 500;
  color: #374151;
  line-height: 1.3;
}

.jf-dropdown-required {
  color: #ef4444;
  margin-left: 0.2rem;
}

/* Main Dropdown */
.jf-dropdown {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: inherit;
  width: 100%;
}

.jf-dropdown:hover {
  border-color: #9ca3af;
}

.jf-dropdown:focus-within {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* Dropdown Sizes */
.jf-dropdown--small {
  padding: 0.5rem 0.75rem;
  font-size: 1.3rem;
  min-height: 3.2rem;
}

.jf-dropdown--medium {
  padding: 0.75rem 1rem;
  font-size: 1.4rem;
  min-height: 4rem;
}

.jf-dropdown--large {
  padding: 1rem 1.25rem;
  font-size: 1.5rem;
  min-height: 4.8rem;
}

/* Dropdown Variants */
.jf-dropdown--primary {
  border-color: #3b82f6;
}

.jf-dropdown--primary:focus-within {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.jf-dropdown--success {
  border-color: #10b981;
}

.jf-dropdown--success:focus-within {
  border-color: #059669;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}

.jf-dropdown--error {
  border-color: #ef4444;
}

.jf-dropdown--error:focus-within {
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

.jf-dropdown--warning {
  border-color: #f59e0b;
}

.jf-dropdown--warning:focus-within {
  border-color: #d97706;
  box-shadow: 0 0 0 3px rgba(245, 158, 11, 0.1);
}

/* Dropdown States */
.jf-dropdown--disabled {
  background: #f9fafb;
  border-color: #e5e7eb;
  color: #9ca3af;
  cursor: not-allowed;
}

.jf-dropdown--disabled:hover {
  border-color: #e5e7eb;
}

.jf-dropdown--full-width {
  width: 100%;
}

.jf-dropdown--open {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.jf-dropdown--open .jf-dropdown-icon {
  transform: rotate(180deg);
}

/* Dropdown Value */
.jf-dropdown-value {
  flex: 1;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #374151;
}

.jf-dropdown--disabled .jf-dropdown-value {
  color: #9ca3af;
}

/* Dropdown Icon */
.jf-dropdown-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
  color: #6b7280;
  transition: transform 0.2s ease;
}

.jf-dropdown--disabled .jf-dropdown-icon {
  color: #d1d5db;
}

/* Dropdown Content */
.jf-dropdown-content {
  min-width: 200px;
  max-height: 20rem;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0.5rem 0;
}

/* Dropdown Items */
.jf-dropdown-item {
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  color: #374151;
  font-size: 1.4rem;
}

.jf-dropdown-item:hover {
  background-color: #f9fafb;
}

.jf-dropdown-item--selected {
  background-color: #eff6ff;
  color: #2563eb;
  font-weight: 500;
}

.jf-dropdown-item--disabled {
  color: #9ca3af;
  cursor: not-allowed;
  background-color: transparent;
}

.jf-dropdown-item--disabled:hover {
  background-color: transparent;
}

/* Error Message */
.jf-dropdown-error-message {
  margin-top: 0.4rem;
  font-size: 1.2rem;
  color: #ef4444;
  line-height: 1.3;
}

/* Responsive */
@media (max-width: 768px) {
  .jf-dropdown-content {
    max-height: 15rem;
  }
  
  .jf-dropdown--small {
    min-height: 3.6rem;
    font-size: 1.4rem;
  }
  
  .jf-dropdown--medium {
    min-height: 4.4rem;
    font-size: 1.5rem;
  }
  
  .jf-dropdown--large {
    min-height: 5.2rem;
    font-size: 1.6rem;
  }
}

/* Custom scrollbar for dropdown content */
.jf-dropdown-content::-webkit-scrollbar {
  width: 6px;
}

.jf-dropdown-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.jf-dropdown-content::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.jf-dropdown-content::-webkit-scrollbar-thumb:hover {
  background: #a1a1a1;
}

/* Popover Placement Animations */
.jf-popover--top .jf-popover-content {
  animation: popover-enter-top 0.15s ease-out;
}

.jf-popover--bottom .jf-popover-content {
  animation: popover-enter-bottom 0.15s ease-out;
}

.jf-popover--left .jf-popover-content {
  animation: popover-enter-left 0.15s ease-out;
}

.jf-popover--right .jf-popover-content {
  animation: popover-enter-right 0.15s ease-out;
}

@keyframes popover-enter-top {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(8px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes popover-enter-bottom {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-8px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes popover-enter-left {
  from {
    opacity: 0;
    transform: scale(0.95) translateX(8px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}

@keyframes popover-enter-right {
  from {
    opacity: 0;
    transform: scale(0.95) translateX(-8px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}
/* ActionList Container */
.jf-actionlist {
  position: relative;
  display: inline-flex;
}

/* ActionList Variants */

.jf-actionlist--bordered .jf-popover-content {
  border: 1px solid #e5e7eb;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.jf-actionlist--elevated .jf-popover-content {
  border: none;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* ActionList Sizes */
.jf-actionlist--small {
  font-size: 1.3rem;
}

.jf-actionlist--medium {
  font-size: 1.4rem;
}

.jf-actionlist--large {
  font-size: 1.5rem;
}

/* ActionList States */
.jf-actionlist--disabled {
  opacity: 0.6;
  pointer-events: none;
}

/* Dropdown Content */
.jf-actionlist-dropdown {
  min-width: 200px;
  max-width: 400px;
  background: white;
  border-radius: 0.5rem;
  overflow: hidden;
}

.jf-actionlist-content {
  padding: 0.5rem 0;
  max-height: inherit;
  overflow-y: auto;
}

/* Action Group */
.jf-actionlist-group {
  margin-bottom: 0;
}

.jf-actionlist-group-title {
  padding: 0.75rem 1rem 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: #f9fafb;
  border-bottom: 1px solid #f3f4f6;
}

.jf-actionlist-group-items {
  padding: 0.25rem 0;
}

.jf-actionlist-separator {
  height: 1px;
  background: #f3f4f6;
  margin: 0.5rem 0;
}

/* Action Item */
.jf-actionlist-item {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #374151;
  border: none;
  background: none;
  text-align: left;
  width: 100%;
  min-height: 4rem;
  outline: none;
  position: relative;
}

.jf-actionlist-item:hover {
  background-color: #f9fafb;
}

.jf-actionlist-item:focus-visible {
  outline: 2px solid #3b82f6;
  outline-offset: -2px;
  background-color: #f9fafb;
}

/* Action Item Sizes */
.jf-actionlist-item--small {
  padding: 0.5rem 0.75rem;
  min-height: 3.2rem;
  font-size: 1.3rem;
}

.jf-actionlist-item--medium {
  padding: 0.75rem 1rem;
  min-height: 4rem;
  font-size: 1.4rem;
}

.jf-actionlist-item--large {
  padding: 1rem 1.25rem;
  min-height: 4.8rem;
  font-size: 1.5rem;
}

/* Action Item Variants */
.jf-actionlist-item--default {
  color: #374151;
}

.jf-actionlist-item--primary {
  color: #3b82f6;
}

.jf-actionlist-item--primary:hover {
  background-color: #eff6ff;
}

.jf-actionlist-item--secondary {
  color: #6b7280;
}

.jf-actionlist-item--secondary:hover {
  background-color: #f3f4f6;
}

.jf-actionlist-item--destructive {
  color: #ef4444;
}

.jf-actionlist-item--destructive:hover {
  background-color: #fef2f2;
}

/* Action Item States */
.jf-actionlist-item--disabled {
  color: #9ca3af;
  cursor: not-allowed;
  opacity: 0.6;
}

.jf-actionlist-item--disabled:hover {
  background-color: transparent;
}

.jf-actionlist-item--selected {
  background-color: #eff6ff;
  color: #2563eb;
}

.jf-actionlist-item--selected:hover {
  background-color: #dbeafe;
}

/* Action Item Layout */
.jf-actionlist-item-leading {
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
}

.jf-actionlist-item-content {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.jf-actionlist-item-trailing {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-left: 0.75rem;
}

/* Action Item Text */
.jf-actionlist-item-label {
  font-weight: 500;
  line-height: 1.4;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.jf-actionlist-item-description {
  font-size: 1.2rem;
  color: #6b7280;
  line-height: 1.3;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.jf-actionlist-item--destructive .jf-actionlist-item-description {
  color: #f87171;
}

.jf-actionlist-item--disabled .jf-actionlist-item-description {
  color: #9ca3af;
}

/* Action Item Icons */
.jf-actionlist-item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.jf-actionlist-item-icon--leading {
  width: 2rem;
  height: 2rem;
}

.jf-actionlist-item-icon--trailing {
  width: 1.6rem;
  height: 1.6rem;
}

.jf-actionlist-item-icon svg {
  width: 1.6rem;
  height: 1.6rem;
  display: block;
}

.jf-actionlist-item--small .jf-actionlist-item-icon svg {
  width: 1.4rem;
  height: 1.4rem;
}

.jf-actionlist-item--large .jf-actionlist-item-icon svg {
  width: 1.8rem;
  height: 1.8rem;
}

/* Action Item Badge */
.jf-actionlist-item-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 2rem;
  height: 2rem;
  padding: 0 0.5rem;
  border-radius: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  text-align: center;
}

.jf-actionlist-item-badge--default {
  background-color: #f3f4f6;
  color: #374151;
}

.jf-actionlist-item-badge--primary {
  background-color: #dbeafe;
  color: #1d4ed8;
}

.jf-actionlist-item-badge--success {
  background-color: #d1fae5;
  color: #065f46;
}

.jf-actionlist-item-badge--warning {
  background-color: #fef3c7;
  color: #92400e;
}

.jf-actionlist-item-badge--error {
  background-color: #fee2e2;
  color: #991b1b;
}

/* Action Item Shortcut */
.jf-actionlist-item-shortcut {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.5rem;
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 0.375rem;
  font-size: 1.1rem;
  font-weight: 500;
  color: #6b7280;
  font-family: monospace;
  line-height: 1;
}

/* Action Item Check */
.jf-actionlist-item-check {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3b82f6;
}

/* Custom Scrollbar */
.jf-actionlist-content::-webkit-scrollbar {
  width: 6px;
}

.jf-actionlist-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.jf-actionlist-content::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.jf-actionlist-content::-webkit-scrollbar-thumb:hover {
  background: #a1a1a1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .jf-actionlist-dropdown {
    min-width: 180px;
    max-width: 100vw;
  }
  
  .jf-actionlist-item {
    padding: 1rem 1.25rem;
    min-height: 4.4rem;
  }
  
  .jf-actionlist-item--small {
    padding: 0.75rem 1rem;
    min-height: 3.6rem;
  }
  
  .jf-actionlist-item--large {
    padding: 1.25rem 1.5rem;
    min-height: 5.2rem;
  }
  
  .jf-actionlist-item-label {
    font-size: 1.5rem;
  }
  
  .jf-actionlist-item-description {
    font-size: 1.3rem;
  }
  
  .jf-actionlist-group-title {
    padding: 1rem 1.25rem 0.75rem;
    font-size: 1.3rem;
  }
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .jf-actionlist-item {
    border: 1px solid transparent;
  }
  
  .jf-actionlist-item:hover {
    border-color: #374151;
  }
  
  .jf-actionlist-item:focus-visible {
    outline: 3px solid #374151;
  }
  
  .jf-actionlist-separator {
    background-color: #374151;
    height: 2px;
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .jf-actionlist-item {
    transition: none;
