/* Base Alert Styles */
.jf-alert {
  padding: 1.6rem;
  border: 1px solid;
  border-radius: 0.5rem;
  position: relative;
  font-family: var(--jf-font-family, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif);
  line-height: 1.5;
  margin-bottom: 1rem;
}

.jf-alert-inner {
  display: flex;
  gap: 1rem;
  align-items: flex-start;
}

.jf-alert-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 0;
}

.jf-alert-title {
  margin: 0 0 0.4rem 0;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.3;
}

.jf-alert-description {
  margin: 0;
  font-size: 1.3rem;
  line-height: 1.5;
}

.jf-alert-icon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.1rem;
}

.jf-alert-actions {
  margin-top: 1.2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Dismiss Button */
.jf-alert-dismiss {
  position: absolute;
  right: 1.2rem;
  top: 1.2rem;
  background: transparent;
  border: none;
  padding: 0.4rem;
  border-radius: 0.4rem;
  cursor: pointer;
  opacity: 0.6;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.jf-alert-dismiss:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.1);
}

.jf-alert--dismissible {
  padding-right: 4.8rem;
}

/* Default Alert */
.jf-alert--default {
  background: #f8f9fa;
  border-color: #e9ecef;
  color: #495057;
}

.jf-alert--default .jf-alert-icon {
  color: #6c757d;
}

.jf-alert--default.jf-alert--intense {
  background: #495057;
  border-color: #495057;
  color: white;
}

.jf-alert--default.jf-alert--intense .jf-alert-icon {
  color: white;
}

/* Success Alert */
.jf-alert--success {
  background: #d1f2eb;
  border-color: #7dcea0;
  color: #196f3d;
}

.jf-alert--success .jf-alert-icon {
  color: #27ae60;
}

.jf-alert--success.jf-alert--intense {
  background: #27ae60;
  border-color: #27ae60;
  color: white;
}

.jf-alert--success.jf-alert--intense .jf-alert-icon {
  color: white;
}

/* Error Alert */
.jf-alert--error {
  background: #fadbd8;
  border-color: #f1948a;
  color: #a93226;
}

.jf-alert--error .jf-alert-icon {
  color: #e74c3c;
}

.jf-alert--error.jf-alert--intense {
  background: #e74c3c;
  border-color: #e74c3c;
  color: white;
}

.jf-alert--error.jf-alert--intense .jf-alert-icon {
  color: white;
}

/* Warning Alert */
.jf-alert--warning {
  background: #fdeaa7;
  border-color: #f7dc6f;
  color: #b7950b;
}

.jf-alert--warning .jf-alert-icon {
  color: #f39c12;
}

.jf-alert--warning.jf-alert--intense {
  background: #f39c12;
  border-color: #f39c12;
  color: white;
}

.jf-alert--warning.jf-alert--intense .jf-alert-icon {
  color: white;
}

/* Info Alert */
.jf-alert--info {
  background: #d6eaf8;
  border-color: #85c1e9;
  color: #1b4f72;
}

.jf-alert--info .jf-alert-icon {
  color: #3498db;
}

.jf-alert--info.jf-alert--intense {
  background: #3498db;
  border-color: #3498db;
  color: white;
}

.jf-alert--info.jf-alert--intense .jf-alert-icon {
  color: white;
}

/* Intense variant dismiss button styling */
.jf-alert--intense .jf-alert-dismiss {
  color: rgba(255, 255, 255, 0.8);
}

.jf-alert--intense .jf-alert-dismiss:hover {
  color: white;
  background: rgba(255, 255, 255, 0.2);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .jf-alert {
    padding: 1.2rem;
  }
  
  .jf-alert--dismissible {
    padding-right: 4rem;
  }
  
  .jf-alert-dismiss {
    right: 0.8rem;
    top: 0.8rem;
  }
  
  .jf-alert-title {
    font-size: 1.4rem;
  }
  
  .jf-alert-description {
    font-size: 1.3rem;
  }
  
  .jf-alert-actions {
