    display: flex;
  }
  
  .topbar-mobile-md .topbar-mobile-logo {
    display: flex;
  }
  
  .topbar-mobile-md .topbar-desktop-logo {
    display: none;
  }
}

/* Mobile breakpoint: LG (up to 992px) - DEFAULT */
@media (max-width: 991.98px) {
  .topbar-mobile-lg .topbar-mobile-toggle,
  .topbar-container .topbar-mobile-toggle {
    display: flex;
  }
  
  .topbar-mobile-lg .topbar-mobile-logo,
  .topbar-container .topbar-mobile-logo {
    display: flex;
  }
  
  .topbar-mobile-lg .topbar-desktop-logo,
  .topbar-container .topbar-desktop-logo {
    display: none;
  }
}

/* Mobile breakpoint: XL (up to 1200px) */
@media (max-width: 1199.98px) {
  .topbar-mobile-xl .topbar-mobile-toggle {
    display: flex;
  }
  
  .topbar-mobile-xl .topbar-mobile-logo {
    display: flex;
  }
  
  .topbar-mobile-xl .topbar-desktop-logo {
    display: none;
  }
}

/* Mobile breakpoint: XXL (up to 1400px) */
@media (max-width: 1399.98px) {
  .topbar-mobile-xxl .topbar-mobile-toggle {
    display: flex;
  }
  
  .topbar-mobile-xxl .topbar-mobile-logo {
    display: flex;
  }
  
  .topbar-mobile-xxl .topbar-desktop-logo {
    display: none;
  }
}

/* ==============================================
   RESPONSIVE TOPBAR ADJUSTMENTS
   ============================================== */

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .topbar-container {
    padding: 0 var(--topbar-padding-x-default);
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .topbar-container {
    padding: 0 var(--topbar-padding-x-default);
  }
  
  .topbar-content {
    gap: var(--topbar-gap-default);
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .topbar-container {
    padding: 0 var(--topbar-padding-x-comfortable);
  }
  
  .topbar-content {
    gap: var(--topbar-gap-comfortable);
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .topbar-container {
    padding: 0 var(--topbar-padding-x-comfortable);
  }
}

/* ==============================================
   ANIMATIONS
   ============================================== */
@keyframes topbar-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes topbar-slide-in {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes topbar-slide-out {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

/* ==============================================
