   ============================================== */
.topbar-mobile-menu-open {
  overflow: hidden;
  position: fixed;
  width: 100%;
}

/* ==============================================
   ACCESSIBILITY ENHANCEMENTS
   ============================================== */

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .topbar-container,
  .topbar-mobile-toggle,
  .topbar-mobile-overlay {
    transition: none;
    animation: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .topbar-container {
    border: 1px solid;
  }
  
  .topbar-mobile-toggle {
    border: 1px solid;
  }
}

/* Focus management */
.topbar-container:focus-within {
  outline: none;
}

