.bottom-sheet {
    position: fixed;
    bottom: -100%;
    left: 0;
    right: 0;
    background-color: var(--jf-naturalLight-000);
    border-top-left-radius: var(--jf-borderRadius-2xlarge);
    border-top-right-radius: var(--jf-borderRadius-2xlarge);
    max-height: 90vh;
    overflow: hidden;
    
    &.open {
        bottom: 0;
        transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
        z-index: 1111;
    }
    
    &.close {
        bottom: -100%;
        transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }
    
    /* Size variants */
    &.bottom-sheet--small {
        max-height: 40vh;
    }
    
    &.bottom-sheet--medium {
        max-height: 60vh;
    }
    
    &.bottom-sheet--large {
        max-height: 85vh;
    }
}

.bottom-sheet-content {
    box-shadow: var(--jf-high-elevation);
    border-radius: var(--jf-borderRadius-2xlarge) var(--jf-borderRadius-2xlarge) 0 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.bottom-sheet-drag-handle {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.2rem 0 0.8rem 0;
    cursor: grab;
    
    &:active {
        cursor: grabbing;
    }
}

.drag-handle-indicator {
    width: 3.6rem;
    height: 0.4rem;
    background-color: var(--jf-naturalLight-400);
    border-radius: var(--jf-borderRadius-max);
    transition: background-color 0.2s ease;
    
    .bottom-sheet-drag-handle:hover & {
        background-color: var(--jf-naturalLight-500);
    }
}

.bottom-sheet__heading {
    display: flex;
    border-bottom: var(--jf-borderWidth-thin) solid var(--jf-naturalLight-300);
    padding: 1.6rem 2rem;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    background-color: var(--jf-naturalLight-050);
    flex-shrink: 0;
}

.bottom-sheet-header {
    display: flex;
    border-bottom: var(--jf-borderWidth-thin) solid var(--jf-naturalLight-300);
    padding: 1.6rem 2rem;
    justify-content: flex-end;
    align-items: center;
    background-color: var(--jf-naturalLight-050);
    flex-shrink: 0;
}

.bottom-sheet-close-button {
    background: var(--jf-naturalLight-200);
    border: none;
    cursor: pointer;
    width: 3.2rem;
    height: 3.2rem;
    border-radius: var(--jf-borderRadius-large);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--jf-naturalDark-900);
    transition: all 0.2s ease;
    flex-shrink: 0;
    
    &:hover {
        background-color: var(--jf-naturalLight-300);
        transform: scale(1.05);
    }
    
    &:active {
        transform: scale(0.95);
    }
    
    &:focus {
        outline: 2px solid var(--jf-primary-color);
        outline-offset: 2px;
    }
}

