.bottom-sheet-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(0px);
    -webkit-backdrop-filter: blur(0px);
    transition: all 0.3s ease;
    
    &.open {
        background-color: rgba(25, 40, 57, 0.4);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
    }
    
    &.close {
        background-color: rgba(25, 40, 57, 0);
        backdrop-filter: blur(0px);
        -webkit-backdrop-filter: blur(0px);
    }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
    .bottom-sheet {
        left: 0;
        right: 0;
        border-top-left-radius: var(--jf-borderRadius-xlarge);
        border-top-right-radius: var(--jf-borderRadius-xlarge);
        
        &.bottom-sheet--small {
            max-height: 50vh;
        }
        
        &.bottom-sheet--medium {
            max-height: 70vh;
        }
        
        &.bottom-sheet--large {
            max-height: 90vh;
        }
    }
    
    .bottom-sheet__heading,
    .bottom-sheet-header {
        padding: 1.2rem 1.6rem;
    }
    
    .bottom-sheet-body {
        padding: 1.6rem;
    }
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
    .bottom-sheet,
    .bottom-sheet-overlay,
    .bottom-sheet-close-button,
    .drag-handle-indicator {
        transition: none;
    }
    
    .bottom-sheet.open {
        transition: none;
    }
    
    .bottom-sheet.close {
        transition: none;
    }
}

