/* Copy Clipboard Component Styles */
.jf-copy-clipboard {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  gap: var(--jf-borderRadius-medium);
  font-family: var(--jf-regular-font);
}

/* Action-only mode */
.jf-copy-clipboard--action-only {
  flex-direction: row;
  align-items: center;
  gap: 0;
}

/* Variants */
.jf-copy-clipboard--inline {
  background: transparent;
}

.jf-copy-clipboard--outlined {
  border: var(--jf-borderWidth-thin) solid var(--jf-naturalLight-300);
  border-radius: var(--jf-borderRadius-medium);
  padding: var(--jf-borderRadius-large);
  background: var(--jf-naturalLight-000);
}

.jf-copy-clipboard--filled {
  background: var(--jf-naturalLight-100);
  border-radius: var(--jf-borderRadius-medium);
  padding: var(--jf-borderRadius-large);
  border: var(--jf-borderWidth-thin) solid var(--jf-naturalLight-200);
}

.jf-copy-clipboard--minimal {
  background: transparent;
  padding: var(--jf-borderRadius-small);
}

/* Size variants */
.jf-copy-clipboard--small {
  font-size: 1.2rem;
  gap: var(--jf-borderRadius-small);
}

.jf-copy-clipboard--small .jf-copy-clipboard-content {
  gap: var(--jf-borderRadius-small);
}

.jf-copy-clipboard--medium {
  font-size: 1.4rem;
  gap: var(--jf-borderRadius-medium);
}

.jf-copy-clipboard--medium .jf-copy-clipboard-content {
  gap: var(--jf-borderRadius-medium);
}

.jf-copy-clipboard--large {
  font-size: 1.6rem;
  gap: var(--jf-borderRadius-large);
}

.jf-copy-clipboard--large .jf-copy-clipboard-content {
  gap: var(--jf-borderRadius-large);
}

/* States */
.jf-copy-clipboard--disabled {
  opacity: 0.6;
  pointer-events: none;
}

.jf-copy-clipboard--copied .jf-copy-clipboard-button {
  background-color: var(--jf-Possitive-100);
  border-color: var(--jf-Possitive-300);
  color: var(--jf-Possitive-700);
}

/* Label */
.jf-copy-clipboard-label {
  display: flex;
  align-items: center;
}

.jf-copy-clipboard-label-text {
  font-weight: 500;
  color: var(--jf-naturalDark-900);
  font-size: inherit;
}

/* Content container */
.jf-copy-clipboard-content {
  display: flex;
  align-items: center;
  gap: var(--jf-borderRadius-medium);
  min-height: 3.2rem;
}

/* Preview text */
.jf-copy-clipboard-preview {
  display: flex;
  align-items: center;
  gap: var(--jf-borderRadius-small);
  flex: 1;
  min-width: 0;
  padding: var(--jf-borderRadius-small) var(--jf-borderRadius-medium);
  background: var(--jf-naturalLight-050);
  border: var(--jf-borderWidth-thin) solid var(--jf-naturalLight-200);
  border-radius: var(--jf-borderRadius-medium);
  transition: all 0.2s ease;
}

.jf-copy-clipboard-preview:hover {
  border-color: var(--jf-naturalLight-300);
  background: var(--jf-naturalLight-100);
}

.jf-copy-clipboard-preview-text {
  flex: 1;
  min-width: 0;
  font-family: var(--jf-regular-font);
  font-size: inherit;
  color: var(--jf-naturalDark-800);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.4;
}

/* Buttons */
.jf-copy-clipboard-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--jf-borderRadius-small);
