/* Checkbox Container */
.jf-checkbox {
  position: relative;
  display: inline-flex;
  align-items: flex-start;
}

/* Checkbox Label */
.jf-checkbox-label {
  display: flex;
  cursor: pointer;
  gap: 0.75rem;
  align-items: flex-start;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  line-height: 1.5;
}

.jf-checkbox--disabled .jf-checkbox-label {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Label Position Variants */
.jf-checkbox--label-left .jf-checkbox-label {
  flex-direction: row-reverse;
}

.jf-checkbox--label-right .jf-checkbox-label {
  flex-direction: row;
}

.jf-checkbox--label-top .jf-checkbox-label {
  flex-direction: column-reverse;
  align-items: center;
}

.jf-checkbox--label-bottom .jf-checkbox-label {
  flex-direction: column;
  align-items: center;
}

/* Checkbox Container and Input */
.jf-checkbox-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.jf-checkbox-input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  cursor: pointer;
  z-index: 1;
}

.jf-checkbox-input:disabled {
  cursor: not-allowed;
}

/* Custom Checkbox Indicator */
.jf-checkbox-indicator {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #d1d5db;
  border-radius: 0.375rem;
  background: white;
  transition: all 0.2s ease;
  box-sizing: border-box;
}

.jf-checkbox-indicator:hover {
  border-color: #9ca3af;
}

.jf-checkbox-input:focus-visible + .jf-checkbox-indicator {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Checkbox Icon */
.jf-checkbox-icon {
  color: white;
  transition: all 0.2s ease;
  opacity: 0;
  transform: scale(0.8);
}

/* Checked State */
.jf-checkbox--checked .jf-checkbox-indicator,
.jf-checkbox--indeterminate .jf-checkbox-indicator {
  border-color: #3b82f6;
  background: #3b82f6;
}

.jf-checkbox--checked .jf-checkbox-icon,
.jf-checkbox--indeterminate .jf-checkbox-icon {
  opacity: 1;
  transform: scale(1);
