/* Modal Overlay */
.jf-modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 0;
    z-index: 9998;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    backdrop-filter: blur(2px);
}

.jf-modal_overlay.open {
    opacity: 1;
    visibility: visible;
}

/* Modal Container */
.jf-modal {
    position: fixed;
    z-index: 9999;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) scale(0.9);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    max-height: 90vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.jf-modal.open {
    opacity: 1;
    visibility: visible;
    transform: translateX(-50%) translateY(-50%) scale(1);
}

/* Size Variants */
.jf-modal--small {
    max-width: 400px;
    width: 90%;
}

.jf-modal--medium {
    max-width: 600px;
    width: 90%;
}

.jf-modal--large {
    max-width: 800px;
    width: 90%;
}

.jf-modal--fullscreen {
    max-width: 95vw;
    max-height: 95vh;
    width: 95vw;
    height: 95vh;
    border-radius: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
    .jf-modal {
        margin: 1rem;
        width: calc(100% - 2rem);
        max-width: none;
    }
    
    .jf-modal--fullscreen {
        margin: 0;
        width: 100vw;
        height: 100vh;
        border-radius: 0;
    }
}

/* Modal Inner Container */
.jf-modal_inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: hidden;
}

.jf-modal-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: hidden;
}

/* Modal Header */
.jf-modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 1.5rem 1rem 1.5rem;
    border-bottom: 1px solid #e5e7eb;
    flex-shrink: 0;
    position: relative;
}

.jf-modal-header h4 {
    margin: 0;
    flex: 1;
    padding-right: 2rem;
}

/* Close Button */
.jf-close-button {
    border: none;
    background: transparent;
    cursor: pointer;
    width: 32px;
    height: 32px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease-in-out;
    color: #6b7280;
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
}

.jf-close-button:hover {
    background: #f3f4f6;
    color: #374151;
}

.jf-close-button:focus {
    outline: 2px solid #3b82f6;
    outline-offset: 2px;
}

.jf-close-button svg {
    display: block;
    width: 20px;
    height: 20px;
}

/* Modal Body */
.jf-modal-body {
    padding: 1.5rem;
    color: #374151;
    overflow-y: auto;
    flex: 1;
    line-height: 1.6;
}

/* Modal Footer */
.jf-modal_footer {
    padding: 1rem 1.5rem 1.5rem 1.5rem;
    border-top: 1px solid #e5e7eb;
    background: #f9fafb;
    flex-shrink: 0;
}

/* Focus Management */
.jf-modal:focus {
    outline: none;
}

/* Animation Improvements */
.jf-modal {
    will-change: transform, opacity;
}

.jf-modal_overlay {
    will-change: opacity;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
    .jf-modal {
        border: 2px solid #000;
    }
    
    .jf-modal-header {
        border-bottom: 2px solid #000;
    }
    
    .jf-modal_footer {
        border-top: 2px solid #000;
    }
}

