/* PageTitle Component Styles */

.pagetitle {
  --pagetitle-spacing-xs: 0.4rem;
  --pagetitle-spacing-sm: 0.8rem;
  --pagetitle-spacing-md: 1.2rem;
  --pagetitle-spacing-lg: 1.6rem;
  --pagetitle-spacing-xl: 2.4rem;
  
  --pagetitle-title-size: 2.4rem;
  --pagetitle-subtitle-size: 1.4rem;
  --pagetitle-breadcrumb-size: 1.3rem;
  
  --pagetitle-border-radius: 0.6rem;
  --pagetitle-transition: all 0.2s ease;
  
  /* Colors */
  --pagetitle-text-primary: #1f2937;
  --pagetitle-text-secondary: #6b7280;
  --pagetitle-text-muted: #9ca3af;
  --pagetitle-bg-hover: #f9fafb;
  --pagetitle-bg-active: #f3f4f6;
  --pagetitle-border: #e5e7eb;
  --pagetitle-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

/* Main container */
.pagetitle {
  margin-bottom: var(--pagetitle-spacing-xl);
  padding: var(--pagetitle-spacing-md) 0;
}



/* Main content row */
.pagetitle-main {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--pagetitle-spacing-lg);
  min-height: 4.8rem;
}

/* Left content section */
.pagetitle-content {
  display: flex;
  align-items: flex-start;
  gap: var(--pagetitle-spacing-lg);
  flex: 1;
  min-width: 0; /* Allow text truncation */
}

/* Back button */
.pagetitle-back-button {
  display: flex;
  align-items: center;
  gap: var(--pagetitle-spacing-sm);
  background: none;
  border: none;
  padding: var(--pagetitle-spacing-sm) var(--pagetitle-spacing-md);
  margin: calc(-1 * var(--pagetitle-spacing-sm)) calc(-1 * var(--pagetitle-spacing-md));
  border-radius: var(--pagetitle-border-radius);
  color: var(--pagetitle-text-secondary);
  font-size: 1.4rem;
  font-weight: 500;
  cursor: pointer;
  transition: var(--pagetitle-transition);
  white-space: nowrap;
}

.pagetitle-back-button:hover {
  background-color: var(--pagetitle-bg-hover);
  color: var(--pagetitle-text-primary);
}

.pagetitle-back-button:focus-visible {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

.pagetitle-back-button:active {
  background-color: var(--pagetitle-bg-active);
}

.pagetitle-back-text {
  font-size: inherit;
  font-weight: inherit;
}

/* Header section */
.pagetitle-header {
  flex: 1;
  min-width: 0;
}

.pagetitle-title-row {
  display: flex;
  align-items: center;
  gap: var(--pagetitle-spacing-md);
  margin-bottom: var(--pagetitle-spacing-sm);
}

.pagetitle-title {
  margin: 0;
  font-size: var(--pagetitle-title-size);
  font-weight: 600;
  line-height: 1.2;
  color: var(--pagetitle-text-primary);
  min-width: 0;
}

.pagetitle-subtitle {
  margin: 0;
  font-size: var(--pagetitle-subtitle-size);
  line-height: 1.4;
  color: var(--pagetitle-text-secondary);
}

/* Actions section */
.pagetitle-actions {
  display: flex;
  align-items: center;
  gap: var(--pagetitle-spacing-sm);
  flex-shrink: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .pagetitle {
    --pagetitle-title-size: 2rem;
    --pagetitle-spacing-lg: 1.2rem;
  }

  .pagetitle-main {
    flex-direction: column;
    align-items: stretch;
    gap: var(--pagetitle-spacing-lg);
  }

  .pagetitle-content {
    flex-direction: column;
    gap: var(--pagetitle-spacing-md);
