/* CSS Custom Properties for Table */
:root {
  --jf-color-primary: #3b82f6;
  --jf-color-text-primary: #1f2937;
  --jf-color-text-secondary: #6b7280;
  --jf-color-border: #e5e7eb;
  --jf-color-border-light: #f3f4f6;
  --jf-color-bg-hover: #f9fafb;
  --jf-color-bg-selected: #eff6ff;
  --jf-color-bg-selected-hover: #dbeafe;
  --jf-color-surface: #ffffff;
  --jf-color-surface-secondary: #f8fafc;
  --jf-shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --jf-shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --jf-radius-md: 8px;
  --jf-radius-lg: 12px;
}

/* Base Table Wrapper */
.jf-table__wrapper {
  position: relative;
  overflow-x: auto;
  overscroll-behavior-x: contain;
  border-radius: var(--jf-radius-md);
  border: 1px solid var(--jf-color-border);
  background: var(--jf-color-surface);
  box-shadow: var(--jf-shadow-sm);
  
  /* Scrollbar Styling */
  &::-webkit-scrollbar {
    height: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: var(--jf-color-border-light);
    border-radius: 4px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--jf-color-text-secondary);
    border-radius: 4px;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background: var(--jf-color-text-primary);
  }
}

/* Table Variants */
.jf-table__wrapper--minimal {
  border: none;
  box-shadow: none;
  border-radius: 0;
}

.jf-table__wrapper--bordered {
  border: 2px solid var(--jf-color-border);
}

.jf-table__wrapper--striped .jf-table .jf-table__row:nth-child(even) {
  background-color: var(--jf-color-surface-secondary);
}

/* Sticky Header */
.jf-table__wrapper--sticky-header {
  max-height: var(--max-height, 400px);
  overflow-y: auto;
}

.jf-table__wrapper--sticky-header .jf-table__heading {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: var(--jf-color-surface-secondary);
}

/* Base Table */
.jf-table {
  width: 100%;
  min-width: 100%;
  border-collapse: collapse;
  font-family: system-ui, -apple-system, sans-serif;
}

/* Density Variants */
.jf-table--compact .jf-table__cell {
  padding: 0.75rem 1rem;
  font-size: 1.3rem;
}

.jf-table--compact .jf-table__heading {
  padding: 0.75rem 1rem;
}

.jf-table--spacious .jf-table__cell {
  padding: 2rem 1.5rem;
  font-size: 1.5rem;
}

.jf-table--spacious .jf-table__heading {
  padding: 1.5rem;
}

/* Table Headings */
.jf-table__heading {
  padding: 1.2rem;
  background-color: var(--jf-color-surface-secondary);
  border-bottom: 2px solid var(--jf-color-border);
  color: var(--jf-color-text-primary);
  font-weight: 600;
  text-align: left;
  position: relative;
  transition: background-color 0.2s ease;
}

.jf-table__heading--sortable {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

.jf-table__heading--sortable:hover {
  background-color: var(--jf-color-border-light);
}

.jf-table__heading--align-center {
  text-align: center;
}

.jf-table__heading--align-right {
  text-align: right;
}

.jf-table__heading-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}

.jf-table__heading-title {
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.025em;
  color: var(--jf-color-text-primary);
}

.jf-table__sort-icon {
  display: flex;
  align-items: center;
  opacity: 0.7;
  transition: opacity 0.2s ease;
}

.jf-table__heading--sortable:hover .jf-table__sort-icon {
  opacity: 1;
}

/* Table Cells */
.jf-table__cell {
  padding: 1.6rem 1.2rem;
  font-size: 1.4rem;
  color: var(--jf-color-text-primary);
  border-bottom: 1px solid var(--jf-color-border-light);
  vertical-align: middle;
  transition: background-color 0.2s ease;
}

.jf-table__cell--align-center {
  text-align: center;
}

.jf-table__cell--align-right {
  text-align: right;
}

/* Table Rows */
.jf-table__row {
  transition: background-color 0.2s ease;
}

.jf-table__row:hover {
  background-color: var(--jf-color-bg-hover);
}

.jf-table__row--selected {
  background-color: var(--jf-color-bg-selected);
}

.jf-table__row--selected:hover {
  background-color: var(--jf-color-bg-selected-hover);
}

/* Row Borders Variant */
.jf-table--row-borders .jf-table__row {
  border-bottom: 1px solid var(--jf-color-border);
}

/* Checkbox Cells */
.jf-table__headingCheckboxCell,
.jf-table__checkboxCell {
  width: 48px;
  padding: 0.8rem 0.8rem 0.8rem 1.2rem;
}

/* Loading State */
.jf-table__loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}

.jf-table__loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid var(--jf-color-border);
  border-top: 3px solid var(--jf-color-primary);
  border-radius: 50%;
  animation: jf-spin 1s linear infinite;
  margin-bottom: 1rem;
}

.jf-table__loading-message {
  font-size: 1.4rem;
  color: var(--jf-color-text-secondary);
  margin: 0;
}

@keyframes jf-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Empty State */
.jf-table__empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}

.jf-table__empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.jf-table__empty-message {
  font-size: 1.4rem;
  color: var(--jf-color-text-secondary);
  margin: 0;
}

/* Responsive Behavior */
@media (max-width: 768px) {
  .jf-table__wrapper--responsive {
    border-radius: 0;
    border-left: none;
    border-right: none;
  }
  
  /* Hide columns on mobile */
