/* Modern Thumbnail Component Styles */

/* CSS Custom Properties for Design System */
:root {
  /* Colors */
  --thumbnail-background: #f8fafc;
  --thumbnail-border: #e2e8f0;
  --thumbnail-border-hover: #cbd5e1;
  --thumbnail-border-focus: #3b82f6;
  --thumbnail-foreground: #64748b;
  --thumbnail-error-bg: #fef2f2;
  --thumbnail-error-border: #fecaca;
  --thumbnail-error-color: #dc2626;
  --thumbnail-overlay-bg: rgba(0, 0, 0, 0.6);
  --thumbnail-badge-bg: #ef4444;
  --thumbnail-badge-color: #ffffff;
  
  /* Spacing */
  --thumbnail-spacing-xs: 0.125rem;
  --thumbnail-spacing-sm: 0.25rem;
  --thumbnail-spacing-md: 0.5rem;
  --thumbnail-spacing-lg: 0.75rem;
  --thumbnail-spacing-xl: 1rem;
  
  /* Sizes */
  --thumbnail-size-xs: 2rem;   /* 32px */
  --thumbnail-size-sm: 2.5rem; /* 40px */
  --thumbnail-size-md: 3rem;   /* 48px */
  --thumbnail-size-lg: 4rem;   /* 64px */
  --thumbnail-size-xl: 5rem;   /* 80px */
  
  /* Border radius */
  --thumbnail-radius-sm: 0.25rem;
  --thumbnail-radius-md: 0.5rem;
  --thumbnail-radius-lg: 0.75rem;
  --thumbnail-radius-full: 50%;
  
  /* Shadows */
  --thumbnail-shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --thumbnail-shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --thumbnail-shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -2px rgb(0 0 0 / 0.1);
  
  /* Transitions */
  --thumbnail-transition: all 200ms cubic-bezier(0.4, 0, 0.2, 1);
  --thumbnail-transition-fast: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
  
  /* Z-indices */
  --thumbnail-z-content: 1;
  --thumbnail-z-overlay: 2;
  --thumbnail-z-badge: 3;
  --thumbnail-z-focus: 4;
}

/* Dark mode variables */
[data-theme="dark"] {
  --thumbnail-background: #1e293b;
  --thumbnail-border: #334155;
  --thumbnail-border-hover: #475569;
  --thumbnail-foreground: #94a3b8;
  --thumbnail-error-bg: #450a0a;
  --thumbnail-error-border: #7f1d1d;
  --thumbnail-error-color: #f87171;
}

/* Base Thumbnail Container */
.jf-thumbnail {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: var(--thumbnail-background);
  border: 1px solid var(--thumbnail-border);
  overflow: hidden;
  transition: var(--thumbnail-transition);
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  font-family: system-ui, -apple-system, sans-serif;
}

.jf-thumbnail:focus {
  outline: none;
}

/* Content Container */
.jf-thumbnail-content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--thumbnail-z-content);
}

/* Image Styles */
.jf-thumbnail-image {
  width: 100%;
  height: 100%;
  display: block;
  transition: var(--thumbnail-transition);
}

/* Placeholder and Loading States */
.jf-thumbnail-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--thumbnail-background);
}

.jf-thumbnail-skeleton {
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  background-color: #e2e8f0;
  position: relative;
  overflow: hidden;
}

.jf-thumbnail-skeleton-shimmer {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.6),
    transparent
  );
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

/* Fallback Content */
.jf-thumbnail-fallback {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--thumbnail-foreground);
  background: var(--thumbnail-background);
