}

.jf-thumbnail--medium {
  width: var(--thumbnail-size-md);
  height: var(--thumbnail-size-md);
}

.jf-thumbnail--large {
  width: var(--thumbnail-size-lg);
  height: var(--thumbnail-size-lg);
}

.jf-thumbnail--extralarge {
