}

/* Shape Variants */
.jf-thumbnail--rounded {
  border-radius: var(--thumbnail-radius-md);
}

.jf-thumbnail--circular {
  border-radius: var(--thumbnail-radius-full);
}

.jf-thumbnail--square {
  border-radius: 0;
}

/* Object Fit Variants */
.jf-thumbnail--cover .jf-thumbnail-image {
  -o-object-fit: cover;
     object-fit: cover;
}

.jf-thumbnail--contain .jf-thumbnail-image {
  -o-object-fit: contain;
     object-fit: contain;
}

.jf-thumbnail--fill .jf-thumbnail-image {
  -o-object-fit: fill;
     object-fit: fill;
}

.jf-thumbnail--scaledown .jf-thumbnail-image {
  -o-object-fit: scale-down;
     object-fit: scale-down;
}

.jf-thumbnail--none .jf-thumbnail-image {
  -o-object-fit: none;
     object-fit: none;
}

/* Visual Variants */
.jf-thumbnail--default {
  /* Default styling already applied */
}

.jf-thumbnail--detailed {
  border-width: 2px;
  box-shadow: var(--thumbnail-shadow-sm);
}

.jf-thumbnail--minimal {
  border: none;
  background: transparent;
}

.jf-thumbnail--card {
  border-width: 1px;
  box-shadow: var(--thumbnail-shadow-md);
  background: #ffffff;
}

[data-theme="dark"] .jf-thumbnail--card {
  background: var(--thumbnail-background);
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.3), 0 2px 4px -2px rgb(0 0 0 / 0.3);
}

/* Aspect Ratio Support */
.jf-thumbnail--aspect-1-1 {
  aspect-ratio: 1 / 1;
  width: auto;
  height: auto;
}

.jf-thumbnail--aspect-4-3 {
  aspect-ratio: 4 / 3;
  width: auto;
  height: auto;
}

.jf-thumbnail--aspect-16-9 {
  aspect-ratio: 16 / 9;
  width: auto;
  height: auto;
}

.jf-thumbnail--aspect-3-2 {
  aspect-ratio: 3 / 2;
  width: auto;
  height: auto;
}

/* Clickable State */
.jf-thumbnail--clickable {
  cursor: pointer;
  transition: var(--thumbnail-transition);
}

.jf-thumbnail--clickable:hover {
  border-color: var(--thumbnail-border-hover);
  transform: translateY(-1px);
  box-shadow: var(--thumbnail-shadow-lg);
}

.jf-thumbnail--clickable:active {
  transform: translateY(0);
  box-shadow: var(--thumbnail-shadow-md);
}

/* Disabled State */
.jf-thumbnail--disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Hover State */
.jf-thumbnail--hovered:not(.jf-thumbnail--disabled) {
  border-color: var(--thumbnail-border-hover);
}

.jf-thumbnail--clickable.jf-thumbnail--hovered:not(.jf-thumbnail--disabled) {
  transform: translateY(-2px);
  box-shadow: var(--thumbnail-shadow-lg);
}

/* Loading State */
.jf-thumbnail--loading {
  /* Loading styles handled by skeleton */
}

/* Overlay */
.jf-thumbnail-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--thumbnail-overlay-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 0.875rem;
  font-weight: 500;
  opacity: 0;
  transition: var(--thumbnail-transition);
  z-index: var(--thumbnail-z-overlay);
  backdrop-filter: blur(2px);
}

.jf-thumbnail:hover .jf-thumbnail-overlay {
  opacity: 1;
}

/* Badge */
.jf-thumb-with-badge {
  position: relative;
}
.jf-thumbnail-badge {
  position: absolute;
  top: var(--thumbnail-spacing-xs);
  right: var(--thumbnail-spacing-xs);
  background: var(--thumbnail-badge-bg);
  color: var(--thumbnail-badge-color);
  border-radius: var(--thumbnail-radius-full);
  min-width: 1.25rem;
  height: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 600;
  z-index: var(--thumbnail-z-badge);
  box-shadow: var(--thumbnail-shadow-sm);
  top: -6px;
  right: -4px;
}

/* Focus Ring */
.jf-thumbnail-focus-ring {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border: 2px solid var(--thumbnail-border-focus);
  border-radius: inherit;
  opacity: 0;
  transition: var(--thumbnail-transition-fast);
  z-index: var(--thumbnail-z-focus);
  pointer-events: none;
}

.jf-thumbnail:focus .jf-thumbnail-focus-ring,
.jf-thumbnail:focus-visible .jf-thumbnail-focus-ring {
  opacity: 1;
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .jf-thumbnail {
    border-width: 2px;
  }
  
  .jf-thumbnail--clickable:hover {
    border-width: 3px;
  }
  
  .jf-thumbnail-focus-ring {
    border-width: 3px;
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .jf-thumbnail,
  .jf-thumbnail-image,
  .jf-thumbnail-overlay,
  .jf-thumbnail-focus-ring,
  .jf-thumbnail-skeleton-shimmer {
    transition: none;
    animation: none;
  }
  
  .jf-thumbnail--clickable:hover {
    transform: none;
  }
}

/* Print Styles */
@media print {
  .jf-thumbnail {
    -moz-column-break-inside: avoid;
         break-inside: avoid;
    box-shadow: none !important;
    border: 1px solid #000 !important;
  }
  
  .jf-thumbnail-overlay {
    display: none;
  }
  
  .jf-thumbnail-badge {
    background: #000 !important;
    color: #fff !important;
  }
}

/* Mobile Optimizations */
@media (max-width: 768px) {
  .jf-thumbnail--clickable:hover {
    transform: none;
    box-shadow: var(--thumbnail-shadow-md);
  }
  
  .jf-thumbnail-overlay {
    opacity: 0;
  }
  
  .jf-thumbnail--clickable:active .jf-thumbnail-overlay {
    opacity: 1;
  }
}

/* Dark mode enhancements */
[data-theme="dark"] .jf-thumbnail-skeleton {
  background-color: #334155;
}

[data-theme="dark"] .jf-thumbnail-skeleton-shimmer {
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
}

/* Animation for successful image load */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.jf-thumbnail-image[src]:not([src=""]) {
  animation: fadeIn 0.3s ease-in-out;
}

/* Enhanced accessibility */
.jf-thumbnail[role="button"] {
  outline-offset: 2px;
}

