  }

  .jf-toolTip__wrapper {
    z-index: 999999;

    left: 50% !important;
    top: 50% !important;
    opacity: 0;
    transform: translate(-50%, -50%) !important;
    transition: 0.3s all;
    width: calc(100% - 100px);
  }

  .jf-toolTip__wrapper.jf-toolTip--in {

    opacity: 1;
  }

  .jf-toolTip__overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    opacity: 0.8;
    z-index: 99999;
